.container {
    /*border: 1px solid;*/
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 700px) {
    .container {
        width: 100%;
        padding: 20px;
    }
}