@import './src/common/styles/sass/variables';


.skill {
  width: 250px;
  background-color: #3C3C3C;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 5px;
  box-sizing: border-box;
  color: $secondTextColor;
  border-radius: 5px;
  margin-top: 65px;
  margin-bottom: 20px;

  .icon {
    margin-top: 10px;
    width: 50px;
  }

  .description {
    font-family: 'Hind Madurai', sans-serif;
  }

  .title {
    font-family: 'Calligraffitti', cursive;
    color: $textColor;
    font-size: 23px;
    font-weight: 100;
  }
}


